import { Frame } from "framer";
import { getWindowWidth, getWindowHeight } from '../window/window_dimensions.js';

export default function ProjectPage(props) {
    const widthLayer = Math.min( getWindowWidth(), 1400 )
    
    return (
        <div>
            <Frame 
                top={220} 
                width={widthLayer} 
                center 
                background="white"
            >
                <div>

                    <div>
                        <h3>{props.dataProject.title}</h3>
                        <h4>{props.dataProject.type}</h4>
                    </div>

                    {
                        props.dataProject.content.map(
                            (aContentPath) => (
                                <img src={"../public/" + aContentPath} />
                            )
                        )
                    }

                    <div>
                        <h5>Back to Projects</h5>
                    </div>

                </div>

            </Frame>
        </div>
    )
}